
import XHeader from 'vux/src/components/x-header/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

import ActionSheet from 'components/ActionSheet/actionsheet.vue';
export default {
components: {
XHeader,
InlineLoading,
ActionSheet
},
mounted(){
this.getVisitCardDetail();
},
data() {
return {
CBM: "",
info: {},

}
},
methods: {

getVisitCardDetail() {
this.$vux.loading.show({
text: '加载中...'
});
this.$ajax.post('',{
"MSH.1": "visitcard",
"MSH.2": "getVisitCard",
"token": this.$root.loginInfo.token,
"cbm": this.$route.params.CBM,
}).then(res => {

this.$vux.loading.hide();
if (typeof res != 'undefined') {
this.info = res;
}
}).catch(error => {
console.log(error);
this.$vux.loading.hide();
})
},
}
};



